
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#inputs input {
  width: 100%;
}
#inputs input.qr {
  width: 79%;
}
#preview {
  width: 69%;
}
#inputs {
  width: 29%;
}
#preview, #inputs {
  display: inline-block;
  height: 100%;
  vertical-align: top;
  box-sizing: border-box;
  padding: 0 20px;
}
label {
    display: inline-block;
    width: 18%;
}
#inputs {
  border: none;
  border-right: 1px solid #ccc;
  resize: none;
  outline: none;
  font-size: 14px;
  padding: 0 20px 0 20px;
}



